	module.exports = {"data":{"markdownRemark":{"html":"<h2 id=\"free-courses\"><a href=\"#free-courses\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Free Courses</h2>\n<ul>\n<li>\n<p><a href=\"https://www.codecademy.com/learn/react-101\">Codecademy: React 101</a> - Codecademy’s introductory course for React.</p>\n</li>\n<li>\n<p><a href=\"https://egghead.io/courses/start-learning-react\">Egghead.io: Start Learning React</a> - This series will explore the basic fundamentals of React to get you started.</p>\n</li>\n<li>\n<p><a href=\"https://www.youtube.com/watch?v=Ke90Tje7VS0\">React Crash Course 2018</a> - A beginner-friendly crash course through the most important React topics.</p>\n</li>\n<li>\n<p><a href=\"https://reactarmory.com/guides/learn-react-by-itself\">React Armory: Learn React by Itself</a> - With React Armory, you can learn React without the buzzwords.</p>\n</li>\n<li>\n<p><a href=\"https://www.robinwieruch.de/the-road-to-learn-react/\">The Road to Learn React</a> - Build a real world application in plain React without complicated tooling.</p>\n</li>\n<li>\n<p><a href=\"https://egghead.io/courses/the-beginner-s-guide-to-reactjs\">Egghead.io: The Beginner’s Guide to ReactJS</a> - Free course for React newbies and those looking to get a better understanding of React fundamentals.</p>\n</li>\n<li>\n<p><a href=\"https://tylermcginnis.com/free-react-bootcamp/\">Free React Bootcamp</a> - Recordings from three days of a free online React bootcamp.</p>\n</li>\n</ul>\n<h2 id=\"paid-courses\"><a href=\"#paid-courses\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Paid Courses</h2>\n<ul>\n<li>\n<p><a href=\"https://egghead.io/browse/frameworks/react\">Egghead.io</a> - Short instructional videos on React and many other topics.</p>\n</li>\n<li>\n<p><a href=\"https://frontendmasters.com/courses/\">Frontend Masters</a> - Video courses on React and other frontend frameworks.</p>\n</li>\n<li>\n<p><a href=\"https://www.fullstackreact.com/\">Fullstack React</a> - The up-to-date, in-depth, complete guide to React and friends.</p>\n</li>\n<li>\n<p><a href=\"https://daveceddia.com/pure-react/\">Pure React</a> - A step-by-step guide to mastering React.</p>\n</li>\n<li>\n<p><a href=\"https://reactforbeginners.com/\">React for Beginners</a> - Learn React in just a couple of afternoons.</p>\n</li>\n<li>\n<p><a href=\"https://designcode.io/react\">React for Designers</a> - A 6-hour React course for designers, by designers.</p>\n</li>\n<li>\n<p><a href=\"https://learnreact.design\">React Essentials for Designers</a> - React courses tailored for designers: the fundamentals, capabilities, limitations and how they relate to design.</p>\n</li>\n<li>\n<p><a href=\"https://learnreact.com/lessons/2018-essential-react-1-overview\">Essential React</a> - A crash course for doers, moving fast from “Hello World” to advanced component composition.</p>\n</li>\n<li>\n<p><a href=\"https://courses.reacttraining.com/p/advanced-react\">React Training: Advanced React.js</a> - Take your React skills to the next level.</p>\n</li>\n<li>\n<p><a href=\"https://tylermcginnis.com/courses\">Tyler McGinnis</a> - Tyler McGinnis provides access to his courses for a monthly fee. Courses include “React Fundamentals” and “Universal React”.</p>\n</li>\n<li>\n<p><a href=\"https://codewithmosh.com/p/mastering-react/\">Mastering React</a> - Build professional interactive apps with React.</p>\n</li>\n</ul>","frontmatter":{"title":"Courses","next":null,"prev":null},"fields":{"path":"community/courses.md","slug":"community/courses.html"}}},"pathContext":{"slug":"community/courses.html"}}

